@each $image in ("icon" "play" "../images/play.png" 37px 45px),
        ("img" "logo" "../images/logo.png" 235px 229px),
        ("icon" "logo" "../images/logo-out.png" 44px 50px),

        ("icon" "design" "../images/design.png" 49px 50px),
        ("icon" "game" "../images/game.png" 41px 50px),
        ("icon" "love" "../images/love.png" 50px 50px),
        ("icon" "mobile" "../images/mobile.png" 28px 50px),
        ("icon" "opt" "../images/opt.png" 50px 50px),
        ("icon" "brand" "../images/brand.png" 50px 50px),
{
  .#{nth($image,1)}-#{nth($image,2)} {
    background: url("#{nth($image,3)}") no-repeat left top;
    width: nth($image,4);
    height: nth($image,5);
  }
}
